<template>
  <div id="alert">
  <BTable striped hover :items="items"/>
  </div>
</template>

<script>
import { AlertsService } from '@/services';
import BTable from 'bootstrap-vue'

export default {
    components:{
        BTable
    },
  data() {
      return {  
          items: []
      }
  },
  methods: {
    SetItems(features) {    
        for(var i = 0 ; i < features.length; i++)
        {
            this.items.push({ fecha: this.formatTime(features[i].attributes.date), Amenaza: features[i].attributes.threat, ver: "ver" })
        }
    } , 
    formatTime(epoch) {
        var options = { hour: '2-digit', minute: '2-digit', second: '2-digit' };

        return new Date(epoch).toLocaleDateString("es-LA", options)
    },
    async Request () {
        const response = await AlertsService.getAlerts();
        if(response)
        this.SetItems(this.OrderByDate(response.features).reverse())
    },
    OrderByDate (array) 
    {
        return array.sort(function(a, b) {
            var dateA = new Date(a.date), dateB = new Date(b.date);
            return dateA - dateB;
            })
    }
  },
  mounted() {
    this.Request()
  }
}

</script>

<style scoped>
#alert {
  padding: 0;
  margin: 1px   ;
  width: 100%;
  height: 50px;
}
</style>